.<template>
    <div>
        <h1>用户列表</h1>
        <table border="1">
            <tr>
                <td>id</td>
                <td>姓名</td>
                <td>年龄</td>
                <td>生日</td>
                <td>操作</td>
            </tr>
            <!-- 遍历数据 -->
            <tr v-for="user in users"> 
                <!-- 通过差值表达式获取数据 -->
                <td>{{user.id}}</td>
                <td>{{user.name}}</td>
                <td>{{user.age}}</td>
                <td>{{user.bir}}</td>
                <td>
                    <a href="javascript:;" @click="delRow(user.id)">删除</a>
                    <a :href="'#/user/userEdit?id='+user.id">修改</a>
                </td>
            </tr>
        </table>
        <a href="#/user/userAdd">添加</a>
        <router-view/>
        
    </div>
</template>

<script>
import UserAdd from './UserAdd.vue';
export default {
    
    name:'User',
    data() {
        return {
            users:[]
        }
    },
    methods: {
        findAll(){ //查询所有
        let _this = this;
        // 使用axios，发起异步请求，下面url地址通过RAP2来模拟后端接口
        this.$http.get("http://localhost:6161/vue/user/findAll").then(function(resp){
            _this.users=resp.data.result
        })
        },
        delRow(id){
            console.log(id);
            let _this = this;
            this.$http.get("http://localhost:6161/vue/user/delete?id="+id).then(function(resp){
                console.log(resp);
                if(resp.data.success){ //判断数据是否已经删除
                    _this.findAll() //刷新页面,查询所有
                }
            });
        }
        
    },
    components:{UserAdd},
    created() {
        this.findAll();
    },
    watch:{ //用来监听
        
        $route:{
            handler: function(val,oldval){ //参数1：变化之后的路径   参数2：变化之前的路径
                console.log(val);
                if(val.path=='/user'){ //判断是否从添加修改切换来的路由
                    this.findAll(); //是的话，就再发起一次查询所有函数
                }
            },
            //深度观察监听
            deep: true
        }
    }

}
</script>

<style></style>


